@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');
.projectcontainer{
    display: flex;
    flex-direction: column;
    color: white;
    justify-content: center;
    margin: 200px 0;
    align-items: center;
}
.projectcontainer > h1{
    padding-bottom: 15px;
}
.projectcontainer > p{
    padding-bottom: 20px;
}
.projectcontainer__projects{
    display: flex;
    width: 60vw;
    margin-top: 50px;
    flex-wrap: wrap;
}